@use "../../foundation/mixin";
@use "../../foundation/variables" as val;

.p-post {
  &__box {
    display    : flex;
    align-items: center;
    padding-bottom: 8px;
  }
  &__heading--about {
    white-space: nowrap;
    margin-left: 10px;
    width      : 100%;
    height     : auto;
  }
  &__text--about {
    border-top : 0.5px solid #B1B1B1;
    width      : 100%;
    padding-top: 16px;
  }
}